.body {
  color: #263238;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 15px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--fluid {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.header__top {
  align-items: center;
  padding: 23px 0 17px;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 24px;
}

.top__text,
.top__text-phone {
  margin-left: auto;
  margin-right: 25px;
  font-size: 15px;
  color: #546e7a;
}

.top__text-link,
.top__text-phone-link {
  color: #e74c3c;
}

.top__text-link:hover,
.top__text-phone-link:hover {
  border-bottom: solid 1px #e74c3c;
}

.top__text-link:focus,
.top__text-phone-link:focus {
  outline: solid 2px #bf2718;
}

.top__text-link:active,
.top__text-phone-link:active {
  color: #bf2718;
  outline: none;
}

.top__text-phone:hover,
.top__text-phone-phone:hover {
  border-bottom: solid 1px #546e7a;
}

.top__text-phone:focus,
.top__text-phone-phone:focus {
  outline: solid 2px #35454d;
}

.top__text-phone:active,
.top__text-phone-phone:active {
  color: #35454d;
  outline: none;
}

.top__btn-link--cart {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 50%;
  position: relative;
}

.top__btn-link--item {
  color: #fff;
  background-color: #e74c3c;
  position: absolute;
  border-radius: 50%;
  padding-bottom: 20px;
  text-align: center;
  font-size: 11px;
  width: 18px;
  height: 18px;
  bottom: 25px;
  left: 18px;
}

.shopping-basket {
  display: block;
  width: 14px;
  height: 13px;
  fill: #a0a0a0;
}

.check {
  font-size: 12px;
  margin-left: 26px;
  border-radius: 4px;
}

.check__link--login, .check__link--registet {
  padding: 9px 20px;
  color: #fff;
  border-radius: 4px;
  width: 72px;
  height: 32px;
}

.check__link--login {
  background-color: #8BC34A;
}

.check__link--registet {
  background-color: #e74c3c;
}

.check__link--login:hover, .check__link--registet:hover {
  background-color: #fff;
}

.check__link--login:hover {
  color: #8BC34A;
}

.check__link--registet:hover {
  color: #bf2718;
}

.menu {
  background-color: #263238;
  padding: 23px 0;
}

.menu__list-item {
  margin-right: 36px;
  display: inline-block;
}

.menu__list-link,
.menu .products__category-link {
  color: #fff;
  line-height: 30px;
  font-size: 14px;
  font-family: "Roboto";
  font-weight: 500;
}

.menu__list-link:hover, .menu__list-link:active {
  border-bottom: 1px solid #fff;
}

.search {
  background: url("../images/bg__search.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.search__inner {
  padding: 135px 0 100px;
  font-family: "Roboto";
  font-weight: 500;
  color: #fff;
  line-height: 30px;
  text-align: center;
}

.search__inner-title {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 48px;
}

.search__inner-text {
  font-size: 20px;
}

.form__search {
  margin-top: 54px;
  position: relative;
}

.form__search-input {
  width: 670px;
  height: 56px;
  border-radius: 4px;
  box-shadow: 0px 1px 10px 0 rgba(124, 178, 64, 0.75);
  background-color: #ffffff;
  font-size: 14px;
  letter-spacing: 0.7px;
  padding: 0 60px 0 25px;
}

.form__search-btn {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 25%;
  right: 280px;
}

.form__search-btn:hover {
  cursor: pointer;
}

.products {
  padding: 121px 0 123px;
  background-color: #f4f6f5;
}

.products__cart {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.title--h2 {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 36px;
  line-height: 1.67;
  text-align: center;
}

.products__category {
  margin-top: 60px;
}

.products__category-link {
  margin-left: 6px;
  padding: 13px 20px;
  box-shadow: 0px 2px 5px 0 rgba(202, 215, 228, 0.75);
  background-color: #fff;
  font-family: "Roboto";
  font-weight: 500;
  color: #263238;
  border: 0;
  outline: none;
  border-radius: 0;
}

.products__category-link:hover, .products__category-link:active {
  color: #fff;
  background-color: #e74c3c;
}

.cart__info,
.trend__info {
  widows: 442px;
  margin-top: 59px;
  margin-right: 16px;
  font-family: "Roboto";
  font-weight: 500;
  color: #263238;
  font-size: 14px;
  border-style: solid;
  border-color: #e7ecf2;
  border-width: 0 1px 1px 1px;
  transition: 0.5s all;
}

.cart__info:hover,
.trend__info:hover {
  box-shadow: 0px 16px 20px 0 rgba(216, 216, 216, 0.75);
}

.cart__info__info-image,
.trend__info__info-image {
  width: 442px;
  height: 250px;
}

.card__info:last-child {
  margin-right: 0;
}

.hide {
  animation-name: hide;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.anime {
  transform: scale(0);
  opacity: 0;
}

@keyframes hide {
  0% {
    visibility: visible;
    height: 200px;
  }
  100% {
    height: 0;
    width: 0;
    max-width: 0;
    font-size: 0;
    margin: 0;
  }
}

.popular {
  width: 63px;
  height: 64px;
  border-top: 100px solid #8BC34A;
  border-right: 100px solid transparent;
  position: absolute;
}

.bolt {
  fill: #fff;
  width: 18px;
  height: 36px;
  position: relative;
  bottom: 86px;
  left: 14px;
}

.block {
  padding: 17px 31px;
  border-bottom: solid 1px #e7ecf2;
  align-items: center;
}

.block__price {
  font-weight: 700;
  font-size: 20px;
}

.block__text-title {
  font-size: 18px;
}

.block__text-category {
  color: #8BC34A;
}

.theme {
  padding: 17px 31px 21px;
}

.theme__author-link--img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.theme__author-link--name {
  color: #546e7a;
  margin-left: 13px;
}

.theme__author-link--name:hover {
  color: #8BC34A;
}

.trend {
  padding-bottom: 121px;
}

.trend__slider .slick-dots {
  margin-top: 67px;
  display: flex;
  justify-content: center;
}

.trend__slider .slick-dots li {
  list-style: none;
  margin: 0 7px;
}

.trend__slider .slick-dots li.slick-active button {
  background-color: #8BC34A;
}

.trend__slider .slick-dots button {
  font-size: 0;
  width: 16px;
  height: 16px;
  box-shadow: inset 1px rgba(154, 147, 140, 0.5);
  border-radius: 50%;
}

.differences {
  background-color: #263238;
  color: #fff;
  padding: 124px 0 118px;
}

.differences__items-block {
  width: 318px;
  text-align: center;
}

.differences__items-block--svg {
  background-color: #fff;
  padding: 21px 21px;
  width: 113px;
  height: 113px;
  border-radius: 50%;
}

.svg-gift {
  fill: #fff;
  background-color: #8BC34A;
  padding: 21px 21px;
}

.svg-heart {
  padding: 28px 15px 10px 13px;
}

.differences__items-title {
  font-family: "Roboto";
  font-weight: 500;
  font-size: 24px;
}

.title-color {
  color: #8BC34A;
}

.differences__items-text {
  color: #b0bec5;
}

.people {
  background: url("../images/people__bg.jpg");
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
}

.people__block {
  width: 653px;
  padding: 71px 59px 86px 61px;
  background-color: #8BC34A;
  color: #fff;
  font-size: 30px;
}

.people__block-text {
  width: 260px;
}

.people__block-text--bold {
  font-family: "Roboto";
  font-weight: 700;
}

.people__block-link {
  display: block;
  padding: 25px 43px 24px;
  background-color: #ffffff;
  color: #263238;
  font-size: 18px;
  font-family: "Roboto";
  font-weight: 400;
}

.people__block-link:hover, .people__block-link:active {
  background-color: #263238;
  color: #fff;
  font-size: 20px;
  transition: all 0.75;
}

.tutorials {
  padding: 66px 0 121px;
}

.tutorials__list {
  margin-top: 57px;
}

.item__tutorial-block {
  width: 370px;
  background-color: #f8f8f8;
}

.tutorials__list-image {
  width: 370px;
  height: 270px;
}

.item__tutorial-text {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 24px;
  padding: 37px 0px;
  text-align: center;
}

.footer {
  padding: 116px 0 32px;
  background-color: #263238;
  color: #b7c3c8;
}

.about,
.community, .follow {
  width: 261px;
}

.about__title,
.community__title,
.help__title,
.follow__title {
  color: #fff;
  position: relative;
  font-family: "Raleway";
  font-weight: 700;
  font-size: 22px;
}

.about__title:after,
.community__title:after,
.follow__title:after {
  content: "";
  width: 51px;
  height: 3px;
  background-color: #8BC34A;
  position: absolute;
  left: 0;
  top: 48px;
}

.about__text {
  display: block;
  margin-top: 64px;
}

.about__adress-point,
.about__telephone {
  color: #fff;
  font-family: "Lato";
  font-weight: 600;
  font-style: normal;
  font-size: 14px;
  position: relative;
  left: 25px;
}

.about__adress-point::before,
.about__telephone::before,
.community__list-item::before,
.help__list-item::before {
  content: url("../images/svg/stack/sprite.svg#map-marker");
  width: 16px;
  height: 23px;
  position: absolute;
  top: 0;
  left: -35px;
}

.about__telephone {
  display: block;
  margin-top: 27px;
}

.about__telephone::before {
  content: url("../images/svg/stack/sprite.svg#phone");
}

.community__list,
.help__list {
  margin-top: 64px;
}

.community__list-item,
.help__list-item {
  position: relative;
  top: 0;
  left: 25px;
  margin-top: 23px;
}

.community__list-item::before,
.help__list-item::before {
  content: url("../images/svg/stack/sprite.svg#chevron-right");
}

.community__list-link,
.help__list-link {
  color: #b7c3c8;
}

.community__list-link:hover,
.community__list-link:active,
.help__list-link:active,
.help__list-link:hover,
.community__list-item::before {
  color: #fff;
}

.follow__list {
  margin-top: 50px;
  display: flex;
}

.follow__list-item {
  margin-left: 7px;
}

.linkedin, .pinterest, .youtube, .twitter, .facebook {
  width: 28px;
  height: 28px;
  background-color: #fff;
}

.facebook {
  fill: #3b5998;
}

.twitter {
  fill: #55acee;
}

.youtube {
  fill: #e52d27;
}

.pinterest {
  fill: #e52d27;
}

.linkedin {
  background-color: #1565c0;
  fill: #fff;
}

@keyframes bounce {
  0%,
  20%,
  60%,
  100% {
    transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
    transform: translateY(-20px);
  }
  80% {
    transform: translateY(-10px);
    transform: translateY(-10px);
  }
}

.follow__list-item:hover {
  animation: bounce 1s;
}

.follow__newsletter {
  color: #fff;
  font-size: 20px;
  margin-top: 41px;
}

.follow__mail {
  margin-top: 18px;
  position: relative;
  top: 0px;
  left: 0;
}

.follow__mail-input {
  width: 261px;
  height: 40px;
  background-color: #263238;
  color: #697378;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  padding: 14px 70px 14px 16px;
}

.follow__mail-btn {
  position: absolute;
  right: 0;
}

.follow__mail-btn--svg {
  width: 43px;
  height: 38px;
  background-color: #8BC34A;
  fill: #fff;
  stroke-width: 0;
}

.copyrayting {
  margin-top: 99px;
  border-top: solid 1px #424d53;
}

.copyrayting__text {
  display: block;
  margin: 27px 0 auto;
}

@media (max-width: 720px) {
  .container--fluid {
    padding: 0 15px;
  }
}
